import React from "react"
import { MyLib } from "@macashipo/mlib"
const BaseMonacoEditor = props => {
  return (
    <MyLib
      type="monacoeditor"
      value={props.value}
      // value={"1"}
      onChange={(newValue, ev) => {
        console.warn("change:", newValue)
        props.onChange && props.onChange(newValue)
      }}
      className={props.className}
      style={props.style}
      options={{
        //https://blutorange.github.io/primefaces-monaco/typedoc/interfaces/monaco.editor.istandaloneeditorconstructionoptions.html#minimap
        tabSize: 2,
        folding: true,
        minimap: {
          enabled: false,
        },
      }}
      {...props.attrs}
    />
  )
}

export default BaseMonacoEditor
